body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

    
h5 {
    font-family: 'Lobster', cursive;
    font-size: 12px;
  }
  .container{

      width: 100%;
      margin: 0px;
      margin-top: 15px;
      text-align: center;
  
    
  }
  
  ul {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row; 
    -webkit-flex-wrap: wrap; 
            flex-wrap: wrap;
  }

  li {
      display: inline;
      -webkit-text-decoration-line: none;
              text-decoration-line: none;
  }


  .card {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      margin: 0px;
  }

 

  .card-content{
      background-color: teal;
      padding: 25px;
      margin-bottom: 15px;
      color: white;
      min-width: 20vw;
      max-width: 20vw;
  }
  .card-footer{
    background-color: white;
    padding: 25px;
    margin-bottom: 15px;
    color: black;
    box-shadow: 4px 4px 4px black;
    overflow: hidden;
}
.f {
    color:rgb(204, 120, 42);;
    font-size: 38px;
    font-family: 'Lobster', cursive;
    font-style: bold;
}
.c {
    color: teal;
    font-size: 38px;
    font-family: 'Lobster', cursive;
    font-style: bold;
}
.sf {
    font-size: 12px;
    font-style: italic;
}

._form{
    position: absolute;
    top: 2px;
    left: 78%;
    background-color:wheat;
    min-width: 15vw;
    max-width: 15vw;
    padding: 15px;
    text-align: center;
    box-shadow: 4px 4px 4px gray;
    overflow: hidden;
}
.logo img{
    position: absolute;
    top: 5px;
    left: 3%;
    max-width: 25vw;
    min-width: 25vw;
    max-height: 25vh;
    min-height: 25vh;

}

.banner {
    font-size: 48px;
    text-shadow: 6px 6px 6px gray;
    color: black;
    font-family: 'Raleway', sans-serif;
    font-style: italic;
}

.city, .days {
    font-family: 'Playfair Display', serif;
}
.wind-prop, td {
    font-family: 'Playfair Display', serif;
    font-size: 12px;
}


.subtitle{
    font-family: 'Lobster', cursive;
}



.btn {
    min-width: 80px;
    min-height: 38px;
    background-color: teal;
    color: white;
    border-radius: 8px;
}
  
  .box {
      border: 1px solid black;
      box-shadow: 4px 4px 4px gray;
  }
  
  img {
      width: 150px;
      height: 100px;
  }
  
  table {
      text-align: center;
      border: 1px solid greenyellow;
      background-color: black;
      color: white;
      width: 100%;
      font-size: 14px;
      overflow: hidden;
  }

  table th {
      font-size: 10px;
  }

  .text-input {
      min-width: '10vw';
      max-width: '10vw';
  
  }
  .text-example{
      font-family: 'Lobster', cursive;
      font-size: 12px;
  }

  .section-map {
      position: relative;
      z-index: 1;
      width: 100%;
      height: 200px;
    }
    
    #googlemap {
      position: absolute;
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      z-index: 0;
    }

    .showbarra {
        -webkit-animation: barra-spin 2s infinite ease-in-out;
                animation: barra-spin 2s infinite ease-in-out;
        display: none;
    }

  .subtitle {
    -webkit-animation: subTitle 2s infinite ease-in-out;
            animation: subTitle 2s infinite ease-in-out;
}

  @-webkit-keyframes subTitle{
      from {
          opacity: 0;
      }
      to{
          opacity: 1;
      }
  }

  @keyframes subTitle{
      from {
          opacity: 0;
      }
      to{
          opacity: 1;
      }
  }
  @-webkit-keyframes barra-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      color: blue;
      opacity: 0;
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      color: green;
      opacity: 1;
    }
  }
  @keyframes barra-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
      color: blue;
      opacity: 0;
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
      color: green;
      opacity: 1;
    }
  }



